@use "../../assets/sass/variables" as *;
@import url("https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Architects+Daughter&family=Gloria+Hallelujah&family=Pacifico&family=The+Girl+Next+Door&display=swap");

.navbar {
  margin: 10px auto;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px 0;
  max-width: 1600px;
  position: relative;

  &__header {
    font-size: 4.3rem;
    font-family: "Amatic SC", cursive;
  }

  &__logo {
    height: 60px;
    margin-right: 10px;
  }

  & a {
    text-decoration: none;
    &:visited {
      color: $color-text-primary;
    }
  }
}
